import Bmob from "hydrogen-js-sdk";
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import loading from "@/components/Loading/index.vue"

let that;
let uid;
export default {
	components: {
		faIcon,
		loading
	},
	data() {
		return {
			loading: true,
			stock: "",
			Goods: null,
			reserve_num: 0,
			reserve_money: 0,
		}
	},

	onLoad() {
		that = this;
		uid = uni.getStorageSync("uid");
		that.stock = uni.getStorageSync("stock")
		that.get_detail()
	},
	
	//分享
	/*onShareAppMessage: function(res) {
		if (res.from === 'button') {
			// 来自页面内转发按钮
			console.log(res.target)
		}
		return {
			title: '库存表-' + that.stock.stock_name + '的详情',
			path: '/pages/manage/good_det/good_det?id=' + that.product.objectId + '&type="false"'
		}
	},*/
	
	methods: {
		show_options() {
			uni.showActionSheet({
				itemList: ["编辑", "删除"],
				success: function(res) {
					if (res.tapIndex == 0) {
						that.edit(that.stock)
					} else if (res.tapIndex == 1) {
						const query = Bmob.Query("stocks");
						query.equalTo("parent", "==", uid);
						query.equalTo("disabled", "!=", true);
						query.find().then(res => {
							if(res.length >1){
								that.delete_this(that.stock.objectId)
							}else{
								uni.showToast({
									title:"最少保留一个店仓",
									icon:"none"
								})
							}
						})
						
					}
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},

		//编辑操作
		edit(item) {
			uni.setStorageSync("warehouse", item);
			uni.setStorageSync("charge", item.charge);
			uni.setStorageSync("shop", item.shop);
			uni.navigateTo({
				url: "../add/add"
			})
		},

		//删除操作
		delete_this(id) {
			uni.showModal({
				title: '提示',
				content: '请谨慎删除，一旦删除，数据不能恢复，是否删除此店仓',
				success: function(res) {
					if (res.confirm) {
						console.log(id);
						that.delete_data(id)
					}
				}
			});
		},

		//删除数据
		delete_data(id) {
			console.log(id)
			const query = Bmob.Query("stocks");
			query.destroy(id).then(res => {
				console.log(res)

				uni.showToast({
					title: "删除成功",
					icon: "none"
				})
				uni.navigateBack({
					delta: 1
				})
				/*const query = Bmob.Query('Goods');
				// 单词最多删除50条
				query.limit(50)
				query.equalTo("stocks", "==", id);
				query.count().then(res => {
					console.log(`共有${res}条记录`)
					for (var i = 0; i < Math.ceil(res / 50); i++) {
						query.find().then(todos => {
							todos.destroyAll().then(res => {
								// 成功批量修改
								console.log(res, 'ok')
								uni.showToast({
									title: "删除成功",
									icon: "none"
								})
								uni.navigateBack({
									delta: 1
								})
							}).catch(err => {
								console.log(err)
							});
						})
					}
				});*/

			}).catch(err => {
				console.log(err)
			})
		},

		goto_detail(good) {
			uni.setStorageSync("now_product", good);
			if(good.order == 1){
				uni.navigateTo({
					url: "/pages/manage/good_det/Ngood_det"
				})
			}else{
				uni.navigateTo({
					url: "/pages/manage/good_det/good_det"
				})
			}
		},

		get_detail() {
			const query = Bmob.Query("Goods");
			query.equalTo("userId", "==", uid);
			query.equalTo("stocks", "==", uni.getStorageSync("stock").objectId);
			query.limit(500);
			query.order("-reserve");
			query.find().then(res => {
				console.log(res)
				that.Goods = res;
				let reserve_num = 0;
				let reserve_money = 0;
				for (let item of res) {
					reserve_money += Number(item.costPrice) * Number(item.reserve)
					reserve_num += Number(item.reserve)
				}

				that.reserve_money = reserve_money
				that.reserve_num = reserve_num
				that.loading = false
			});
		},


	}
}
